import React from 'react'
import { fetchQuestions, newQuestion, oldQuestions, popularQuestions } from '../../../redux/questions'
import { Button } from 'antd'
import { useAppDispatch } from '../../../redux/hooks'

export const FilterQuestions: React.FC = () => {

    const dispatch = useAppDispatch()

    return (
        <div className='flex gap-[20px]'>
        <h1 className='text-[20px]'>:</h1>
            <Button onClick={() => dispatch(popularQuestions())}></Button>
            <Button onClick={() => dispatch(newQuestion())}></Button>
            <Button onClick={() => dispatch(oldQuestions())}></Button>
            <Button onClick={() => dispatch(fetchQuestions())}></Button>
        </div>
    )
}
