import React from 'react'
import {Button, Tooltip} from 'antd'
import { useAppDispatch, useAppSelector } from '../../../redux/hooks'
import { changePage } from '../../../redux/page'
import { isUser } from '../../../utils/checkValue'
import { Posts } from '../../Posts/Posts'
import { FilterQuestions } from './FilterQuestions'


export const Questions: React.FC = () => {

  const status = useAppSelector(el => el.questions.questions.status)
  const questions = useAppSelector(el => el.questions.questionsArr.items)
  const state = useAppSelector(el => el.auth.state)
  const answers = useAppSelector(el => el.questions.myAnswer.value)
  const dispatch = useAppDispatch()

  const statusQuestions = status === 'success'

  console.log(answers)

  /*/
          <div className='flex gap-[20px] justify-center items-center'>
            <img className='w-[70px]' src={Land} alt="" />
            <div className='w-[500px] space-y-[3px]'>
              <h1 className='text-[20px] leading-6'>    , !</h1>
              <p className='break-words text-[17px] leading-5 text-gray-400'>       ,    .</p>
            </div>
            <Button className='ml-[10px] text-[17px]'> </Button>
        </div>
        /*/

  const formopen = () => {
    dispatch(changePage({type: 'FORM_CREATE'}))
  }

  return (
    <div className='grid w-full'>
        <div className='flex justify-between items-center px-[55px]'>
            <div className='space-y-[3px]'>
              <h1 className='text-[20px]'></h1>
            </div>
            <Tooltip color='gray' title={isUser(state) ? state.role === '' ? ' ' : state.ban 
              ? ` .  - ${state.banText ? state.banText : ''}` : '' : ''}>
              <Button id='titlewrapper'  disabled={isUser(state) && state.role === '' && !state.ban ? false : true} onClick={() => formopen()} className='ml-[10px] text-[17px]'> </Button>
            </Tooltip>
        </div>
        <div className='items-center px-[55px] justify-between flex pt-[20px] max-[1200px]:flex-wrap max-[1200px]:gap-[10px]'>
          <h1 className='text-[18px]'> : {questions.length ? questions.length : '0'}</h1>
          <FilterQuestions />
        </div>
        <Posts loadingQuestions={statusQuestions} questions={questions}/>
    </div>
  )
}
