import React, { useState } from 'react'
import { useAppSelector } from '../../redux/hooks'
import { Posts } from '../Posts/Posts'
import { isUser } from '../../utils/checkValue'

type TypeVariants = '' | '' | ''

export const Variants: React.FC = () => {

    const [variant, setVariant] = useState<TypeVariants>('')

    const statusQuestions = useAppSelector(el => el.questions.questions.status)

    const myQuestions = useAppSelector(el => el.questions.myQuestions.items)
    const myModerationQuestions = useAppSelector(el => el.questions.myModerationQuestions.items)
    const myFavorite = useAppSelector(el => el.questions.favorite)
    const myAnswers = useAppSelector(el => el.questions.myAnswer.value)

    const user = useAppSelector(el => el.auth.user.value)
    const state = useAppSelector(el => el.auth.state)

    console.log(myFavorite)

    const loadingQuestions = statusQuestions  === 'success'

    return (
        <>
            {isUser(user) && isUser(state) && <div className='text-[18px] pt-[20px] text-slate-800 flex justify-center gap-[30px] cursor-pointer'>
                <p onClick={() => setVariant('')} className='px-[5px]' style={{borderBottom: variant === '' ? '2px solid gray' : '2px solid white', transition: 'all 0.2s'}}>{user.role === '' ? '' : ''}</p>
                <p onClick={() => setVariant('')} className={`px-[5px] ${user.role === '' && user._id === state._id ? 'flex' : 'hidden'}`} style={{borderBottom: variant === '' ? '2px solid gray' : '2px solid white', transition: 'all 0.2s'}}> </p>
                <p onClick={() => setVariant('')} className='px-[5px]' style={{borderBottom: variant === '' ? '2px solid gray' : '2px solid white', transition: 'all 0.2s'}}></p>
            </div>}
            <Posts loadingQuestions={loadingQuestions} questions={variant === '' ? isUser(user) && user.role === '' ? myAnswers : myQuestions : variant === '' ? myModerationQuestions : myFavorite} />
        </>
    )
}
