import { createSlice } from "@reduxjs/toolkit";
import { StatePage } from "../types/types";


const initialState: StatePage = {
    page: 'home', 
    form: false, //      
    support: false, //    
    questionOpen: {
        open: false, //    ,      ,       ,    
        link: '' //   ,      
    }
}


const themeSlice = createSlice({
    name: 'page-slice',
    initialState,
    reducers: {
        changePage: (state, action) => {
            console.log(action)

            switch(action.payload.type){
                case 'HOME': 

                    state.page = 'home'
                    state.form = false //           ,   false

                    state.questionOpen.open = false // ,    ,          
                    state.questionOpen.link = '' //       

                    break;
                case 'ABOUT': 
                    state.page = 'about'
                    state.form = false

                    console.log('weg')

                    if(action.payload.link){
                        state.questionOpen.open = true
                        state.questionOpen.link = action.payload.link
                    }

                    break;
                case 'THEMES': 
                    state.page = 'themes'
                    break;
                case 'FORM_CREATE': {
                    state.form = true
                }
            }
        },
        changeSupport: (state, action) => {
            state.support = !action.payload.type
        }
    }
})

export const pageReducer = themeSlice.reducer
export const {changePage, changeSupport } = themeSlice.actions